import { isDev } from './config.js';

import { baseLayers, satelliteLayers } from './layers';

function showToast(message, duration = 2000) {
  const toast = document.createElement('div');
  toast.innerText = message;
  toast.style.position = 'fixed';
  toast.style.bottom = '20px';
  toast.style.left = '50%';
  toast.style.transform = 'translateX(-50%)';
  toast.style.background = 'rgba(0,0,0,0.8)';
  toast.style.color = '#fff';
  toast.style.padding = '8px 12px';
  toast.style.borderRadius = '6px';
  toast.style.fontSize = '14px';
  toast.style.zIndex = 9999;
  toast.style.opacity = 0;
  toast.style.transition = 'opacity 0.3s ease';

  document.body.appendChild(toast);

  // fade in
  requestAnimationFrame(() => {
    toast.style.opacity = 1;
  });

  setTimeout(() => {
    toast.style.opacity = 0;
    setTimeout(() => {
      document.body.removeChild(toast);
    }, 300);
  }, duration);
}

export function addContextMenu(map) {
  const contextMenu = document.getElementById('context-menu');

  function addMenuItem(label, action) {
    const item = document.createElement('div');
    item.innerText = label;
    item.style.padding = '4px 8px';
    item.style.cursor = 'pointer';
    item.onmouseenter = () => (item.style.background = '#eee');
    item.onmouseleave = () => (item.style.background = '');
    item.onmousedown = () => {
      action();
      contextMenu.style.display = 'none';
    };
    contextMenu.appendChild(item);
  }

  function addSeparator() {
    const hr = document.createElement('hr');
    hr.style.margin = '0';
    contextMenu.appendChild(hr);
  }

  map.on('contextmenu', function (e) {
    const lat = e.latlng.lat.toFixed(6);
    const lng = e.latlng.lng.toFixed(6);
    const z = map.getZoom();
    const currentBaseLayer = Object.values(baseLayers).find(v => map.hasLayer(v));
    const currentSatelliteLayer = Object.values(satelliteLayers).find(v => map.hasLayer(v));
    contextMenu.innerHTML = '';

    addMenuItem('Edit in OpenStreetMap', () => {
      window.open(
        `https://www.openstreetmap.org/id?#map=${Math.max(16, map.getZoom())}/${lat}/${lng}`
      );
    });

    addSeparator();

    addMenuItem('Open in Google Maps', () => {
      const params = new URLSearchParams({
        api: 1,
        map_action: 'map',
        basemap: 'terrain',
        center: [lat, lng].join(','),
        zoom: z,
      });
      window.open(`https://www.google.com/maps/@?${unescape(params)}`);
    });

    if (isDev) {
      addMenuItem('Open in Google Street View', () => {
        window.open(`https://www.google.com/maps?q=&layer=c&cbll=${lat},${lng}`);
      });

      addMenuItem('Open in Mapillary', () => {
        const params = new URLSearchParams({ lat, lng, z });
        window.open(`https://www.mapillary.com/app/?${unescape(params)}`);
      });
    }

    addMenuItem('Open in Komoot', () => {
      window.open(`https://www.komoot.com/plan/@${lat},${lng},${z}z?sport=mtb`);
    });

    addSeparator();

    addMenuItem('Copy Coordinates', () => {
      navigator.clipboard.writeText(`${lat}, ${lng}`).then(() => {
        showToast('Coordinates copied to clipboard.');
      });
    });

    addMenuItem('Get Elevation', () => {
      fetch(`https://api.open-elevation.com/api/v1/lookup?locations=${lat},${lng}`)
        .then(response => response.json())
        .then(data => {
          if (data.results && data.results.length > 0) {
            const elevation = data.results[0].elevation;
            showToast(`Elevation: ${elevation} meters`);
          }
        })
        .catch(error => console.error('Error fetching elevation:', error));
    });

    addSeparator();

    if (isDev) {
      addMenuItem('Download OSM GPS traces', () => {
        window.open(
          'https://api.openstreetmap.org/api/0.6/trackpoints?bbox=' +
            [
              map.getBounds().getWest(),
              map.getBounds().getSouth(),
              map.getBounds().getEast(),
              map.getBounds().getNorth(),
            ].join(',') +
            '&page=0'
        );
      });

      addMenuItem('Generate SqliteDB', () => {
        const bounds = map.getBounds();
        const north = bounds.getNorth().toFixed(6);
        const west = bounds.getWest().toFixed(6);
        const south = bounds.getSouth().toFixed(6);
        const east = bounds.getEast().toFixed(6);
        const bboxString = `${north},${west},${south},${east}`;

        let copyString;
        if (currentSatelliteLayer?.options.opacity > 0) {
          copyString = `node scripts/sqlitedb.js ./tmp/${
            currentSatelliteLayer.options.id
          }.sqlitedb '${currentSatelliteLayer._url.replace(
            '{s}',
            currentSatelliteLayer.options.subdomains[0]
          )}' ${bboxString}`;
        } else if (currentBaseLayer) {
          copyString = `node scripts/sqlitedb.js ./tmp/${
            currentBaseLayer.options.id
          }.sqlitedb '${currentBaseLayer._url.replace(
            '{s}',
            currentBaseLayer.options.subdomains[0]
          )}' ${bboxString}`;
        }
        if (copyString) {
          navigator.clipboard.writeText(copyString).then(() => {
            showToast('Command copied to clipboard.');
          });
        }
      });
    }

    // Position and show menu
    contextMenu.style.left = e.originalEvent.pageX + 'px';
    contextMenu.style.top = e.originalEvent.pageY + 'px';
    contextMenu.style.display = 'block';
  });

  const hideMenu = () => (contextMenu.style.display = 'none');

  map.on('mousedown', hideMenu);
  document.addEventListener('mousedown', hideMenu);
}
