import { isDev } from './config.js';
import { mainOverlay, mainOverlayDev } from './layers.js';

export function addKeyboardShortcuts(map) {
  function reverseOverlays() {
    const el = document.getElementById('map');
    if (mainOverlay.options.opacity === 1) {
      mainOverlay.setOpacity(0);
      el.classList.remove('hide-overlays');
    } else {
      mainOverlay.setOpacity(1);
      // el.classList.add('hide-overlays');
    }
  }
  function toggleOverlays(all = false) {
    const el = document.getElementById('map');
    if (el.classList.contains('hide-overlays')) {
      // If overlays are hidden, show them
      el.classList.remove('hide-overlays');
      mainOverlay.setOpacity(1);
    } else {
      el.classList.toggle('hide-overlays'); // toggles the 'hidden' class on/off
      mainOverlay.setOpacity(all ? 0 : 1);
    }
  }

  function toggleSatellite() {
    const slider = document.querySelector('.transparency-slider');
    if (!slider) return;

    const current = parseInt(slider.value, 10);
    slider.value = current === 100 ? '0' : '100';

    // Trigger the input event so it behaves reactively
    slider.dispatchEvent(new Event('input', { bubbles: true }));
  }

  function toggleMainOverlay() {
    if (!isDev) return;

    if (map.hasLayer(mainOverlay)) {
      map.removeLayer(mainOverlay);
      map.addLayer(mainOverlayDev);
    } else if (map.hasLayer(mainOverlayDev)) {
      map.removeLayer(mainOverlayDev);
      map.addLayer(mainOverlay);
    }
  }

  document.addEventListener('keydown', e => {
    const active = document.activeElement;
    if (
      active &&
      (active.tagName === 'INPUT' || active.tagName === 'TEXTAREA' || active.isContentEditable)
    )
      return;

    if (e.key.toLowerCase() === 'a') {
      toggleOverlays(true);
    } else if (e.key.toLowerCase() === 'q') {
      toggleOverlays(false);
    } else if (e.key.toLowerCase() === 'w') {
      reverseOverlays();
    } else if (e.key.toLowerCase() === 's') {
      toggleSatellite();
    } else if (e.key === 'Delete') {
      localStorage.clear();
      location.reload();
    } else if (e.key.toLowerCase() === 'e') {
      toggleMainOverlay();
    }
  });
}
