function createDRRLayer(attribution, layers) {
  return L.tileLayer.wms(
    'https://gisportal.drr.go.th/portal/sharing/servers/aa5fdea8d96542d49d3d8731d77458b7/rest/services/GISBaseMaps/DRR_Dynamic/MapServer/export',
    {
      transparent: true,
      attribution,
      format: 'png',
      layers,
      bboxSR: 102100,
      f: 'image',
      className: 'overlay',
    }
  );
}

const DRR_DYNAMIC_LAYERS = {
  all: createDRRLayer('DRR (All)', 'show:1,2,3,4,5,6,7'),
  highways: createDRRLayer('DRR (Highways)', 'show:-1,-1,-1,3'),
  majorHighways: createDRRLayer('DRR (Highways Major)', 'show:-1,-1,-1,2'),
  ruralRoads: createDRRLayer('DRR (Rural Roads)', 'show:-1,-1,-1,7'),
};

